.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px; }

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white; }

.charCode {
  font-size: 5em;
  text-transform: uppercase;
  font-family: "Lucida Console", Monaco, monospace; }

sub {
  font-size: 0.4em;
  opacity: 0.5;
  font-weight: bold; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

[data-root='CharInput'] {
  display: block;
  margin: 1em 0; }
  [data-root='CharInput'] label, [data-root='CharInput'] input {
    font-size: 2em;
    font-weight: bold;
    color: white; }
  [data-root='CharInput'] label {
    opacity: 0.5; }
  [data-root='CharInput'] input {
    background: none;
    border: none;
    border-bottom: 0.1em solid rgba(255, 255, 255, 0.5);
    width: 1.2em;
    padding: 0;
    margin-left: 0.25em;
    font-family: 'Monaco' monospace;
    text-align: center; }
    [data-root='CharInput'] input:focus {
      outline: none;
      background: rgba(255, 255, 255, 0.1);
      border-bottom: 0.1em solid #fff; }



